.list-item {
    border: 1px solid #fff;
    margin: 20px 0px;
}
.list-item li{
    list-style: none;
    color: #fff;
    font-weight: bold;
    padding: 10px;

}

.icon{
    display: grid;
    justify-items: end;
    margin-top: -18px;
    color: red;
}
.icon:hover{
    cursor: pointer;
}